    gap: 10rem;
  }
  .main-rates__item {
    height: 56rem;
    border-radius: 4rem;
    flex-direction: column;
  }
  .rates-item::before, .rates-item::after {
    width: 9rem;
    height: 9rem;
  }
  .rates-item::after {
    right: -4.5rem;
  }
  .rates-item::before {
    left: -4.5rem;
  }
  .rates-item__left {
    padding: 4.8rem;
    height: 13.4rem;
    width: 100%;
    justify-content: center;
    border-right: 0;
    border-bottom: 0.3rem dashed #282828;
  }
  .rates-item__title {
    font-size: 3rem;
  }
  .rates-item__right {
    flex-direction: column;
    width: 100%;
    gap: 4rem;
    height: auto;
    padding-left: 0;
    padding-top: 2.4rem;
  }
  .rates-item__price-top strong {
    margin: 0 1rem;
  }
  .rates-item__price-top span {
    align-self: flex-end;
    margin-bottom: 0;
  }
  .rates-item__price-top sup {
    align-self: flex-start;
  }
  .rates-item__price-bottom {
    color: #282828;
  }
  .rates-item__options {
    width: 80%;
    text-align: center;
    display: flex;
    justify-content: center;
    margin-bottom: 2.8rem;
  }
  .rates-item__icon {
    display: none;
  }
