 {\n        display: block;\n    }\n}\n\n.wrapper {\n    margin: 0 auto;\n    max-width: 1920px;\n}\n\n// --------------------------------------------------------------------------\n\n// header / footer\n@import './sections/header';\n@import './sections/footer';\n\n// ui\n@import '../ui/ui.scss';\n\n// --------------------------------------------------------------------------\n\n@import './dev/vzmsk1.scss';\n@import './dev/markusDM.scss';\n@import './dev/ukik0.scss';\n@import './dev/kie6er.scss';\n","*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\nhtml {\n    font-family: 'EuclidCircularA'; // шрифт по умолчанию по сайту\n    font-size: 0.5208335vw; // на разрешении 1920 0.520835vw === 10px\n    font-style: normal;\n    font-weight: normal;\n    -webkit-animation: bugfix infinite 1s;\n    line-height: 1.2;\n    margin: 0;\n    padding: 0;\n}\n\nbody {\n    font-style: normal;\n    font-weight: normal;\n    -webkit-animation: bugfix infinite 1s;\n    line-height: 1.2;\n    margin: 0;\n    padding: 0;\n    color: $white; // цвет по умолчанию текста по сайту\n    background-color: $bodyColor;\n}\n\ninput,\ntextarea {\n    -webkit-animation: bugfix infinite 1s;\n    line-height: inherit;\n    margin: 0;\n    padding: 0;\n    background-color: transparent;\n    border: none;\n    color: inherit;\n}\na {\n    color: unset;\n}\na,\na:hover {\n    text-decoration: none;\n}\n\nbutton,\ninput,\na,\ntextarea {\n    outline: none;\n    cursor: pointer;\n    font: inherit;\n    &:focus {\n        outline: none;\n    }\n    &:active {\n        outline: none;\n    }\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    font: inherit;\n    margin: 0;\n    padding: 0;\n}\np {\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\nimg {\n    width: 100%;\n    height: auto;\n    display: block;\n}\n\nbutton {\n    border: none;\n    color: inherit;\n    font: inherit;\n    text-align: inherit;\n    padding: 0;\n    background-color: transparent;\n}\nul {\n    padding: 0;\n    margin: 0;\n}\n\nul li {\n    margin: 0;\n    padding: 0;\n    list-style: none;\n}\n\n.container {\n    width: 172rem;\n    margin: 0 auto;\n}\n\ninput[type='number']::-webkit-inner-spin-button,\ninput[type='number']::-webkit-outer-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n}\n\ninput[type='number'] {\n    -moz-appearance: textfield;\n}\n\nsvg,\nimg {\n    width: 100%;\n    height: auto;\n    object-fit: contain;\n}\n\n@media (min-width: 1920px) {\n    html {\n        font-size: 10px;\n    }\n}\n\n@media (max-width: 48em) {\n    html {\n        font-size: 5px;\n        font-size: 1.5625vw;\n        font-size: calc((100 / 375) * 5vw); // где 375 это ширина моб версии макета\n        -webkit-text-size-adjust: none;\n    }\n\n    body {\n        -webkit-text-size-adjust: none;\n    }\n\n    .container {\n        padding: 0 2rem; // в моб версии отступ от края задаем для всех контейнеров, а там где не нужно можем точечно убрать\n        width: 100%;\n    }\n}\n",".header {\n    position: fixed;\n    width: 100%;\n    top: 0;\n    left: 0;\n    z-index: 2000;\n    background-color: $bodyColor;\n    border-bottom: 0.1rem solid rgba(201, 251, 64, 0.5);\n    // .header__inner\n    &__inner {\n        display: flex;\n        align-items: center;\n        justify-content: space-between;\n        gap: 1rem;\n    }\n    // .header__burger-btn\n    &__burger-btn {\n        display: none;\n    }\n    // .header__logo\n    &__logo {\n        padding: 1rem 0;\n        height: 100%;\n        width: 22.7rem;\n        border-right: 0.1rem solid rgba(201, 251, 64, 0.5);\n    }\n    // .header__img\n    &__img {\n        width: 17.9878rem;\n        height: 4.6rem;\n    }\n    // .header__nav\n    &__nav {\n        gap: 5rem;\n    }\n    &__burger-menu {\n        display: none;\n        overflow: auto;\n    }\n}\n\n.nav {\n    display: flex;\n    align-items: center;\n    // .nav__link\n    &__link {\n        display: block;\n        font-size: 2rem;\n        line-height: 140%; /* 28px */\n        transition: color 0.2s linear;\n\n        &:hover,\n        &:focus-visible {\n            transition: color 0.2s linear;\n            color: $green;\n        }\n    }\n}\n\n@media (max-width: 48em) {\n    .header {\n        border-bottom: none;\n        &.scroll {\n            border-bottom: 0.1rem solid rgba(201, 251, 64, 0.5);\n        }\n\n        &.show {\n            .header__phone {\n                display: none;\n            }\n            .header__inner {\n                flex-direction: row-reverse;\n            }\n        }\n        &__container {\n            position: relative;\n            z-index: 1;\n            background-color: $bodyColor;\n        }\n        // .header__inner\n        &__inner {\n            display: flex;\n            align-items: center;\n            justify-content: space-between;\n            gap: 1rem;\n            padding: 1.6rem 0 1.8rem;\n        }\n        // .header__burger-btn\n        &__burger-btn {\n            display: flex;\n            width: 5.2rem;\n            height: 4.6rem;\n        }\n        // .header__logo\n        &__logo {\n            padding: 0;\n            height: 9.2rem;\n            width: 36.6rem;\n            border-right: none;\n        }\n        // .header__img\n        &__img {\n            width: 100%;\n            height: 100%;\n        }\n        // .header__nav\n        &__nav {\n            display: none;\n        }\n        // .header__phone\n        &__phone {\n            width: 4.8rem;\n            height: 4.8rem;\n        }\n    }\n}\n@media (max-width: 48em) {\n    .burger-btn {\n        &__btn {\n            width: 100%;\n            height: 100%;\n\n            display: flex;\n            justify-content: center;\n            flex-direction: column;\n            gap: 1.4rem;\n\n            span {\n                display: block;\n                width: 100%;\n                height: 0.6rem;\n                background-color: $white;\n                border-radius: 2.4rem;\n            }\n        }\n\n        &__close {\n            display: none;\n        }\n\n        &.open {\n            .burger-btn__btn {\n                display: none;\n            }\n            .burger-btn__close {\n                display: block;\n                width: 4rem;\n                height: 4rem;\n            }\n        }\n    }\n\n    .burger-menu {\n        display: flex;\n        flex-direction: column;\n        justify-content: space-between;\n        gap: 15rem;\n        position: absolute;\n        top: 12.6rem;\n        left: 0;\n        z-index: 0;\n        transition: transform 0.5s linear;\n        transform: translateY(-120%);\n\n        width: 100%;\n        height: calc(100vh - 12.6rem);\n        background-color: $bodyColor;\n\n        &.show {\n            transition: transform 0.5s linear;\n            transform: translateY(0);\n        }\n        // .burger-menu__wrapper\n        &__wrapper {\n            display: flex;\n            align-items: center;\n            flex-direction: column;\n            padding-top: 16rem;\n        }\n        &__phone {\n            display: flex;\n            flex-direction: column;\n            align-items: center;\n            border-top: 0.2rem solid $green;\n\n            padding: 3.2rem 0 4.4rem;\n            a {\n                display: flex;\n                align-items: center;\n                gap: 2.6rem;\n                span {\n                    width: 6.4rem;\n                    height: 6.4rem;\n                    svg path {\n                        fill: $pink;\n                    }\n                }\n\n                strong {\n                    color: $pink;\n                    font-size: 4.8rem;\n                    font-style: normal;\n                    font-weight: 500;\n                    line-height: normal;\n                }\n            }\n        }\n        // .burger-menu__link\n        &__link {\n            display: flex;\n            justify-content: center;\n\n            width: 100%;\n            color: $white;\n            font-family: $font-druk;\n            font-size: 10rem;\n            font-style: normal;\n            font-weight: 500;\n            line-height: 110%; /* 55px */\n            text-transform: uppercase;\n            padding: 3.2rem 0;\n\n            border-bottom: 0.2rem solid $green;\n\n            &:first-child {\n                border-top: 0.2rem solid $green;\n            }\n\n            &:hover,\n            &:active,\n            &:focus-visible {\n                background-color: $pink;\n                color: $bodyColor;\n            }\n        }\n    }\n}\n",".footer {\n    background-color: $yellow;\n    color: $main;\n    // .footer__inner\n    &__inner {\n        padding: 7rem 0;\n    }\n    // .footer__top\n    &__top {\n        margin-bottom: 8rem;\n    }\n    // .footer__sitemap\n    &__sitemap {\n        gap: 20rem;\n    }\n    // .footer__middle\n    &__middle {\n        display: flex;\n        gap: 20rem;\n        align-items: flex-end;\n    }\n    // .footer__copyright\n    &__copyright {\n        color: $main;\n        font-size: 2rem;\n        width: 59.528rem;\n    }\n    // .footer__social\n    &__social {\n        gap: 1.6rem;\n    }\n    // .footer__info\n    &__info {\n        display: flex;\n        flex-direction: column;\n        margin-left: auto;\n        gap: 2.5rem;\n    }\n    // .footer__phone\n    &__phone {\n        color: $main;\n        font-size: 3.2rem;\n        line-height: normal;\n\n        &:hover,\n        &:focus-visible,\n        &:active {\n            text-decoration: underline;\n        }\n    }\n    // .footer__politic\n    &__politic {\n        color: $main;\n        font-size: 2rem;\n        line-height: normal;\n\n        position: relative;\n\n        &::after {\n            content: '';\n            width: 100%;\n            height: 0.15rem;\n            background-color: $main;\n\n            position: absolute;\n            left: 0;\n            bottom: 0;\n            z-index: 1;\n        }\n\n        &:hover,\n        &:focus-visible {\n            &::after {\n                display: none;\n            }\n        }\n    }\n    // .footer__bottom-text\n    &__bottom-text {\n        margin-top: 3rem;\n        font-family: $font-druk;\n        font-size: 19.9rem;\n        font-style: normal;\n        font-weight: 500;\n        line-height: 23.4rem;\n        text-transform: uppercase;\n\n        @include small-tablet {\n            font-size: 11rem;\n            font-style: normal;\n            font-weight: 500;\n            line-height: 110%;\n            text-transform: uppercase;\n        }\n    }\n}\n.footer-sitemap {\n    display: flex;\n    // .footer-sitemap__item\n    &__item {\n        &:nth-child(1) {\n            .sitemap-item__list {\n                grid-template-columns: repeat(3, 1fr);\n            }\n        }\n        &:nth-child(2) {\n            .sitemap-item__list {\n                grid-template-columns: 1fr;\n            }\n        }\n        &:nth-child(3) {\n            margin-left: auto;\n            .sitemap-item__list {\n                grid-template-columns: repeat(2, 1fr);\n            }\n        }\n    }\n}\n.sitemap-item {\n    // .sitemap-item__title\n    &__title {\n        margin-bottom: 3.2rem;\n    }\n    // .sitemap-item__list\n    &__list {\n        line-height: normal;\n        display: grid;\n        gap: 2rem 8rem;\n    }\n    // .sitemap-item__link\n    &__link {\n        line-height: inherit;\n        white-space: nowrap;\n        background: none;\n\n        &:hover,\n        &:focus-visible,\n        &:active {\n            text-decoration: underline;\n        }\n    }\n}\n\n@media (max-width: 48em) {\n    .footer {\n        background-color: $yellow;\n        color: $main;\n        // .footer__inner\n        &__inner {\n            padding: 10rem 0 17.8rem;\n        }\n        // .footer__top\n        &__top {\n            margin-bottom: 8.4rem;\n        }\n        // .footer__sitemap\n        &__sitemap {\n            padding-left: 4.4rem;\n            gap: 3rem;\n        }\n        // .footer__middle\n        &__middle {\n            display: flex;\n            flex-direction: column;\n            gap: 2rem;\n            align-items: center;\n        }\n        // .footer__copyright\n        &__copyright {\n            order: 2;\n            text-align: center;\n            font-size: 2.4rem;\n            width: 100%;\n        }\n        // .footer__social\n        &__social {\n            gap: 6rem;\n            order: 3;\n            margin-top: 5.6rem;\n        }\n        // .footer__info\n        &__info {\n            text-align: center;\n            display: flex;\n            flex-direction: column;\n            margin-left: 0;\n            gap: 1rem;\n            order: 1;\n        }\n        // .footer__phone\n        &__phone {\n            font-size: 3.6rem;\n        }\n        // .footer__politic\n        &__politic {\n            color: $main;\n            font-size: 3rem;\n\n            position: relative;\n\n            &::after {\n                display: none;\n            }\n        }\n        // .footer__bottom-text\n        &__bottom-text {\n            text-align: center;\n            line-height: 1.1;\n            margin-top: 10.4rem;\n        }\n    }\n    .footer-sitemap {\n        display: grid;\n        grid-template-columns: repeat(2, 28rem);\n        grid-auto-flow: dense;\n        // .footer-sitemap__item\n        &__item {\n            &:nth-child(1) {\n                grid-row: span 2;\n            }\n            &:nth-child(3) {\n                margin-left: 0;\n            }\n        }\n    }\n    .sitemap-item {\n        // .sitemap-item__title\n        &__title {\n            margin-bottom: 2rem;\n        }\n        // .sitemap-item__list\n        &__list {\n            line-height: normal;\n            display: flex;\n            flex-direction: column;\n            gap: 1.6rem;\n        }\n        // .sitemap-item__link\n        &__link {\n            line-height: inherit;\n            white-space: nowrap;\n            background: none;\n\n            &:hover,\n            &:focus-visible,\n            &:active {\n                text-decoration: underline;\n            }\n        }\n    }\n}\n","@mixin pseudo() {\n    content: '';\n    position: absolute;\n    @content;\n}\n\n@mixin small-tablet {\n    @media (max-width: 48em) {\n        @content;\n    }\n}\n\n@mixin sizes($width, $height) {\n    max-width: $width;\n    width: 100%;\n    height: $height;\n\n    @content;\n}\n",".tl1,\n.tl2,\n.tl3 {\n    font-family: DrukCyr;\n    font-weight: 500;\n    text-transform: uppercase;\n}\n\n.tl1 {\n    font-size: 20rem;\n    line-height: 117%;\n    @media (max-width: 48em) {\n        font-size: 11rem;\n        line-height: 110%;\n    }\n}\n\n.tl2 {\n    line-height: 110%;\n    font-size: 10rem;\n}\n\n.tl3 {\n    font-size: 6rem;\n    line-height: 110%;\n    @media (max-width: 48em) {\n        font-size: 4rem;\n        letter-spacing: 0.32rem;\n    }\n}\n\n// --------------------------------------------------------------------------\n\n.txt32 {\n    font-size: 3.2rem;\n    @media (max-width: 48em) {\n        font-size: 3rem;\n        line-height: 140%;\n    }\n}\n\n.txt20 {\n    font-size: 2.8rem;\n\n    &_caps {\n        font-size: 4rem;\n        line-height: 110%;\n        letter-spacing: 0.32rem;\n        text-transform: uppercase;\n    }\n    &_md {\n        font-weight: 500;\n    }\n    @media (min-width: 48em) {\n        font-size: 2rem;\n        line-height: 140%;\n        &_caps {\n            font-size: 2.2rem;\n            font-weight: 500;\n            line-height: 109.091%;\n            letter-spacing: 0.176rem;\n        }\n    }\n}\n\n.txt28 {\n    &_md {\n        font-size: 2.8rem;\n        font-weight: 500;\n        @media (max-width: 48em) {\n            font-size: 3.6rem;\n        }\n    }\n}\n\n.txt16 {\n    font-size: 1.6rem;\n    @media (max-width: 48em) {\n        font-size: 2.4rem;\n    }\n}\n\n.text-32 {\n    font-size: 3.2rem;\n    font-style: normal;\n    font-weight: 400;\n    line-height: normal;\n}\n\n.text-m-28 {\n    font-size: 2.8rem;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n\n    @include small-tablet {\n        font-size: 3.6rem;\n    }\n}\n\n.text-m-20 {\n    font-family: 'DrukCyr';\n    font-size: 2.0093rem;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 84%; /* 16.878px */\n    letter-spacing: 0.1607rem;\n}\n\n.text-20 {\n    font-size: 2rem;\n    font-style: normal;\n    font-weight: 400;\n    line-height: 140%; /* 28px */\n\n    @include small-tablet {\n        font-size: 2.8rem;\n    }\n}\n",".btn {\n    padding: 1.3rem 1rem 1.3rem 2.8rem;\n    display: inline-flex;\n    justify-content: center;\n    align-items: center;\n    column-gap: 2.2rem;\n    min-width: 16.4rem;\n    border-radius: 9.5rem;\n    border: 1px solid $green;\n    transition: background-color 0.3s ease;\n    &_black {\n        border: 1px solid $black;\n        .btn__text {\n            color: $black;\n        }\n    }\n    &_white {\n        border: 1px solid $white;\n        .btn__text {\n            color: $white;\n        }\n    }\n    @media (any-hover: hover) {\n        &:hover {\n            &:not(&.btn_black, &.btn_white) {\n                background-color: $green;\n            }\n            &.btn_black {\n                background-color: $black;\n            }\n            &.btn_white {\n                background-color: $white;\n                .btn__text {\n                    color: $black;\n                }\n                svg path {\n                    fill: $black;\n                }\n            }\n            .btn__text {\n                color: $white;\n            }\n            .btn__icon {\n                path {\n                    fill: $white;\n                }\n            }\n        }\n    }\n    @med